
import { defineComponent, Ref } from "vue";
import {
	Listbox,
	ListboxButton,
	ListboxLabel,
	ListboxOption,
	ListboxOptions
} from "@headlessui/vue";
import { CheckIcon, SelectorIcon } from "@heroicons/vue/solid";
import { CloudDownloadIcon, ChevronDownIcon } from "@heroicons/vue/outline";

interface System {
	name: string;
	os: string;
	src: string;
	download: (arg0: Event) => void;
}

interface Asset {
	browser_download_url: string;
}

interface Properties {
	systems: System[];
	selected: System;
}

export default defineComponent({
	name: "DownloadDropdown",
	components: {
		Listbox,
		ListboxButton,
		ListboxLabel,
		ListboxOption,
		ListboxOptions,
		CheckIcon,
		SelectorIcon,
		CloudDownloadIcon,
		ChevronDownIcon
	},
	setup(): Properties {
		function getOS(): String | null {
			const userAgent = window.navigator.userAgent,
				platform = window.navigator.platform,
				macosPlatforms = ["Macintosh", "MacIntel", "MacPPC", "Mac68K"],
				windowsPlatforms = ["Win32", "Win64", "Windows", "WinCE"],
				iosPlatforms = ["iPhone", "iPad", "iPod"];
			let os = null;

			if (macosPlatforms.indexOf(platform) !== -1) {
				os = "Mac OS";
			} else if (iosPlatforms.indexOf(platform) !== -1) {
				os = "iOS";
			} else if (windowsPlatforms.indexOf(platform) !== -1) {
				os = "Windows";
			} else if (/Android/.test(userAgent)) {
				os = "Android";
			} else if (!os && /Linux/.test(platform)) {
				os = "Linux";
			}

			return os;
		}

		const isAsset = (valueToTest: any): valueToTest is Asset[] => {
			return valueToTest.every(
				(value: any) =>
          (value instanceof Object &&
					typeof value.browser_download_url === "string")
			);
		};

		const getReleases = async (): Promise<any> => {
			const response = await fetch(
				"https://api.github.com/repos/storjrd/backup/releases/latest"
			);

			return response.json();
		};

		const releasesPromise = getReleases();

		const download =
			(extension: string): ((e: Event) => Promise<void>) =>
			async (e: Event): Promise<void> => {
				e.preventDefault();
				e.stopPropagation();

        const releases = await releasesPromise;
        if (releases.assets instanceof Object) {
          const assets = releases.assets;

          if (isAsset(assets)) {
            const release = assets.find((release: Asset) =>
              release.browser_download_url
                .toLowerCase()
                .endsWith(extension)
            );

            if (release) {
              window.open(release.browser_download_url);
            }
          }
        } else {
					throw new Error(
						"Unable to retrieve download links from GitHub repository."
					);
				}
      }

		const systems: System[] = [
			{
				name: "Download for Windows",
				os: "Windows",
				src: require("@/assets/icon-windows.svg"),
				download: download(".exe")
			},
			{
				name: "Download for Linux",
				os: "Linux",
				src: require("@/assets/icon-linux.svg"),
				download: download(".appimage")
			},
			{
				name: "Download for MacOS",
				os: "Mac",
				src: require("@/assets/icon-apple.svg"),
				download: download(".dmg")
			}
		];

		const OS = getOS();
		let selected = systems[0];

		if (OS === "Linux") {
			selected = systems[1];
		} else if (OS === "Mac OS") {
			selected = systems[2];
		}

		return {
			systems,
			selected
		};
	}
});
